@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,500;0,700;0,800;1,300;1,500;1,700;1,800&display=swap);

@charset "UTF-8";
* {
  border: 0;
  margin: 0;
  outline: none;
  padding: 0;
}
a {
  color: #fcfdff;
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}
p {
  line-height: 170%;
}
html, body {
  background-color: #333944;
  color: #fcfdff;
  font-family: Montserrat, Roboto, "Helvetica Neue", sans-serif;
  font-size: 16px;
  height: 100%;
}
ul {
  padding-left: 20px;
}
ul li::before {
  content: "•";
  /* Add content: \2022 is the CSS Code/unicode for a bullet */
  color: #aec6f1;
  /* Change the color */
  display: inline-block;
  /* Needed to add space between the bullet and the text */
  font-size: 1.5rem;
  font-weight: 900;
  /* If you want it to be bold */
  margin: 0 0 0 -1rem;
  /* Also needed for space (tweak if needed) */
  transform: translate(-0.15rem, 0.15rem);
  width: 1rem;
  /* Also needed for space (tweak if needed) */
}
button, input, textarea {
  font-family: Montserrat, Roboto, "Helvetica Neue", sans-serif;
}
@media only screen and (min-width: 768px) {
  html, body {
    font-size: 18px;
  }

  button {
    font-size: 90%;
  }
}
.btn {
  background-color: #333944;
  border: 2px solid #aec6f1;
  border-radius: 1000px;
  color: #aec6f1;
  cursor: pointer;
  display: inline-block;
  height: 48px;
  margin-bottom: 0.5rem;
  text-align: center;
  transform: perspective(1px) translateZ(0);
  transition: transform 0.06s ease;
  -webkit-transform: perspective(1px) translateZ(0);
  -webkit-transition: transform 0.06s ease;
}
.btn--active, .btn:active {
  background-color: #aec6f1;
  color: #333944;
}
.btn:active {
  transform: scale(0.95);
  -webkit-transform: scale(0.95);
}
.btn--go {
  border: 2px solid #9bec8f;
  color: #9bec8f;
}
.btn--go:active {
  background-color: #9bec8f;
  color: #333944;
}
.btn--hide {
  display: none;
}
.btn--warn {
  border: 2px solid #ffa0b6;
  color: #ffa0b6;
}
.btn--warn:active {
  background-color: #ffa0b6;
  color: #333944;
}
.btn--inactive {
  border: 2px solid #acb5c7;
  color: #acb5c7;
  cursor: auto;
  pointer-events: none;
}
@media only screen and (min-width: 768px) {
  .btn:hover {
    background-color: #aec6f1;
    color: #333944;
  }
  .btn--warn:hover {
    background-color: #ffa0b6;
    color: #333944;
  }
  .btn--go:hover {
    background-color: #9bec8f;
    color: #333944;
  }
  .btn--inactive:hover {
    background-color: #333944;
    color: #aec6f1;
  }
}
.container {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: auto;
}
.line-divider {
  background: #acb5c7;
  height: 1.5px;
  margin: 2rem 0;
}
.section-divider {
  background: #333944;
  height: 15vh;
}
.section-divider--half {
  height: calc(15vh / 2);
}
.editor__header {
  text-align: center;
  margin-bottom: 2rem;
}
.editor__form {
  text-align: left;
}
.editor__form-field {
  margin-bottom: 2rem;
}
.editor__form-field input, .editor__form-field textarea {
  width: 100% !important;
}
.editor__form-btn-container {
  width: 48px;
}
.editor__form-btn {
  height: 48px;
  margin-top: 1rem;
  max-width: 312px;
  width: 100%;
}
.editor__form-btn--submit {
  font-size: 120%;
}
.mat-checkbox .mat-checkbox-inner-container {
  margin-right: 0.5rem;
}
.mat-checkbox .mat-checkbox-label {
  font-family: Montserrat, Roboto, "Helvetica Neue", sans-serif;
}
.mat-radio-button {
  margin: 0 1rem 1rem 0;
}
.mat-radio-button .mat-radio-label-content {
  font-family: Montserrat, Roboto, "Helvetica Neue", sans-serif;
}
.editor__profile-btn--add {
  height: 36px;
  font-size: 160%;
  width: 64px;
}
.editor__profile-btn--edit {
  height: 24px;
  width: 32px;
}
.editor__profile-btn--warn {
  font-size: 90%;
  height: 24px;
  width: 42px;
}
.editor__profile-btn-row {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.editor__profile-btn-row input {
  width: 50%;
}
.form__checkbox-item {
  margin-bottom: 1rem;
}
.editor__profile-form-field {
  max-width: 60%;
  margin-right: 1rem;
}
@media only screen and (min-width: 768px) {
  .editor__profile-btn--edit, .editor__profile-btn--warn {
    font-size: 120%;
    height: 36px;
    width: 64px;
  }
}
.form-field input, .form-field textarea {
  border-radius: 1px;
  box-sizing: border-box;
  font-size: 100%;
  width: 50%;
}
.form-field input {
  height: 3vh;
  padding: 1rem 0.5rem;
}
.form-field input--password {
  font-size: 20px;
}
.form-field textarea {
  height: 15vh;
  max-width: 100%;
  min-width: 100%;
  padding: 0.5rem;
}
.form-field--external {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.form-field--large textarea {
  height: 32vh;
}
.form-field-label {
  font-weight: 700;
  font-size: 125%;
}
@media only screen and (min-width: 768px) {
  input, textarea {
    font-size: 110%;
  }

  .form-field--password input {
    font-size: 24px;
  }
}
.text-link {
  color: #aec6f1;
  display: inline-block;
}
@media only screen and (min-width: 768px) {
  .text-link:hover {
    text-decoration: underline;
  }
}
markdown ::ng-deep h1,
markdown ::ng-deep h2,
markdown ::ng-deep h3,
markdown ::ng-deep h4,
markdown ::ng-deep h5,
markdown ::ng-deep h6 {
  margin: calc(2rem * 1.25) 0 calc(2rem * 0.75);
}
markdown ::ng-deep p,
markdown ::ng-deep img {
  margin: 1rem 0;
}
markdown ::ng-deep pre {
  margin: calc(1rem * 1.0) 0 calc(1rem * 1.5);
}
markdown ::ng-deep a {
  color: #aec6f1;
  display: inline-block;
}
markdown ::ng-deep div img {
  border: 1px solid black;
  box-sizing: border-box;
  text-align: center;
  width: 100%;
}
markdown ::ng-deep img {
  border: 1px solid black;
  height: auto;
  text-align: center;
  width: 100%;
}
markdown ::ng-deep p code {
  background-color: #4e5d74;
  box-sizing: border-box;
  color: #fcfdff;
  margin: 0 calc(0.5rem / 2);
  padding: calc(0.5rem / 4) calc(0.5rem / 2);
}
markdown ::ng-deep .text--note {
  color: #aec6f1;
}
markdown ::ng-deep .text--note-paragraph {
  color: #e1edff;
}
markdown ::ng-deep .text--caution {
  color: #ffe7a0;
}
markdown ::ng-deep .text--caution-paragraph {
  color: #ffefd3;
}
markdown ::ng-deep .text--warn {
  color: #ffa0b6;
}
markdown ::ng-deep .text--warn-paragraph {
  color: #ffd6dc;
}
markdown ::ng-deep .text--tip {
  color: #9bec8f;
}
markdown ::ng-deep .text--tip-paragraph {
  color: #d7ffd5;
}
@media only screen and (min-width: 768px) {
  markdown ::ng-deep a:hover {
    text-decoration: underline;
  }

  markdown ::ng-deep img {
    width: 64%;
  }
}
.section-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.section {
  background-color: #333944;
  color: #fcfdff;
  padding: 2rem;
  margin: 0 auto;
  max-width: 1060px;
  min-height: calc(98vh - 8vh - 15vh - 6vh * 2);
}
